                <li class="bullet-list__item">•  Безлимитные отклики на кастинги без гонорара</li>
                <li class="bullet-list__item">•  Доступ к обучению: Статьи</li>
              </ol>
            </template>

            <template v-if="status === 1">
              <ol class="bullet-list m-text_bold">
                <li class="bullet-list__item">•  Три отклика в день на кастинги с гонораром</li>
                <li class="bullet-list__item">•  Безлимитные отклики на кастинги без гонорара</li>
                <li class="bullet-list__item">•  Доступ к обучению: Онлайн-видео и Статьи</li>
                <li class="bullet-list__item">•  Скидку 20% на профессиональное портфолио</li>
                <li class="bullet-list__item">•  Скидку 10% на оффлайн мастер-классы</li>
              </ol>
            </template>

            <template v-if="status === 2 && payment_period === 2">
              <ol class="bullet-list m-text_bold">
                <li class="bullet-list__item">•  Безлимитные отклики на все кастинги</li>
                <li class="bullet-list__item">•  Доступ к обучению: Онлайн-видео и Статьи</li>
                <li class="bullet-list__item">•  Бесплатное профессиональное портфолио раз в полгода</li>
                <li class="bullet-list__item">•  Скидка 30% на персональный сайт</li>
                <li class="bullet-list__item">•  Скидка 20% на оффлайн мастер-классы</li>
              </ol>
            </template>


            <template v-if="status === 2 && payment_period !== 2">
              <ol class="bullet-list m-text_bold mb_6">
                <li class="bullet-list__item">•  Безлимитные отклики на все кастинги</li>
                <li class="bullet-list__item">•  Доступ к обучению: Онлайн-видео и Статьи</li>
              </ol>

              <div class="label input__label color_gray-3">
                Недоступные бонусы
              </div>
              <ol class="bullet-list m-text_bold color_gray-3">
                <li class="bullet-list__item">•  Бесплатное профессиональное портфолио раз в полгода</li>
                <li class="bullet-list__item">•  Скидка 30% на персональный сайт</li>
                <li class="bullet-list__item">•  Скидка 20% на оффлайн мастер-классы</li>
              </ol>
            </template>
          </div>

          <div class="column mb_4">
            <button @click="_handleClickChooseTariff" :class="['btn', {'btn_primary' : status < 2, 'btn_secondary_filled' : status === 2, 'mb_3' : status > 0}]" :disabled="status === 2">Изменить тариф</button>
            <button v-if="status > 0" @click="_handleClickReductionTariff" class="btn btn_secondary_filled">Отменить подписку</button>
          </div>

          <div class="column m-text_normal color_gray-3">
            <template v-if="status === 2 && payment_period !== 2">
              <p class="mb_3">
                Измените период оплаты, чтобы получить доступ к остальным особенностям тарифа. Для изменения периода
                <router-link :to="{ name: 'user.show' }" class="color_main">свяжитесь с нами</router-link>
              </p>
            </template>

            <p>
              {{ payment_period !== -1 ?
              'При отмене подписки или понижении тарифного плана, функции вашего текущего тарифа будут доступны до 09.01.20, а после перейдут в бесплатный режим' :
              'Вы отменили подписку, функции вашего текущего тарифа будут доступны до 02.02.21, а после перейдут в бесплатный режим' }}
