        </div>

        <template v-if="hasTransactions">
          <div class="block block_declaration">
            <div class="label input__label">
              История операций
            </div>

            <div class="card__tariff card__tariff_small">
              <template v-for="(item, index) in transactions.slice(0, 2)">
                <template v-if="(item.slug === '-')">
                  <div :class="['column', {'mb_4' : index !== transactions.length - 1 }]">
                    <div class="row justify_between m-text_bold mb_0_5">
                      <span>
                        Оплата подписки
