                        {{ item.date }}
                      </span>
                    </div>
                  </div>
                </template>

                <template v-if="(item.slug === '+')">
                  <div :class="['column', {'mb_4' : index !== transactions.length - 1 }]">
                    <div class="row justify_between m-text_bold mb_0_5">
                      <span>
                        Пополнение баланса
                      </span>

                      <span class="color_main">
                        + {{ item.value }} ₽
                      </span>
                    </div>

                    <div class="row justify_between m-text_normal">
                      <span>
                        Оплата картой - 5455
                      </span>

                      <span>
                          {{ item.date }}
                      </span>
                    </div>
                  </div>
                </template>
              </template>

              <collapse v-model="collapse['transactions']">
                <template v-for="item in transactions.slice(2)">
                  <template v-if="(item.slug === '-')">
                    <div class="column mb_4">
                      <div class="row justify_between m-text_bold mb_0_5">
                        <span>
                          Оплата подписки
                        </span>
                        <span>
                          - {{ item.value }} ₽
                        </span>
                      </div>

                      <div class="row justify_between m-text_normal">
                        <span>
                          Списание с баланса
                        </span>
                        <span>
                          {{ item.date }}
                        </span>
                      </div>
                    </div>
                  </template>

                  <template v-if="(item.slug === '+')">
                    <div class="column mb_4">
                      <div class="row justify_between m-text_bold mb_0_5">
                        <span>
                          Пополнение баланса
                        </span>

                        <span class="color_main">
                          + {{ item.value }} ₽
                        </span>
                      </div>

                      <div class="row justify_between m-text_normal">
                        <span>
                          Оплата картой - 5455
                        </span>

                        <span>
                          {{ item.date }}
                        </span>
                      </div>
                    </div>
                  </template>
                </template>
              </collapse>

              <template v-if="transactions.length > 4">
                <div class="row align_center mt_4" @click="_handleClickToggleCollapse('transactions')">
                  <div class="m-text_bold color_main mr_1">{{ collapse['transactions'] ? 'Скрыть' : 'Показать все' }}</div>

                  <svg :class="['icon', 'icon_size_16', 'fill_main', { rotate_180: (collapse['transactions']) }]">
                    <use xlink:href="/dist/img/icons/svg/sprite.svg#lm-chevron"></use>
                  </svg>
                </div>
              </template>
            </div>
          </div>
        </template>
      </div>

      <div class="page__footer page__footer_declaration">
        <div class="page__block_declaration">
          <div class="row justify_between">
            <button class="btn btn_secondary" @click="_handleClickPreviousPage">
              {{ $t('unified-profile.profile-settings.back') }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Collapse } from 'uiv'

  export default {
    components: { Collapse },
    metaInfo () {
      return {
        title: 'Тарифы'
      }
    },
    computed: {
      hasTransactions () {
        return true
      },
    },
    data () {
      return {
        loading: false,
        status: 2,  // 0 - любиель, 1 - стандарт, 2 - премиум
        payment_period: 2, // -1 - отменено автопродление, 0 - месяц, 1 - полгода, 2 - год
        payment_reject: false,
        transactions: [
          {
            slug: '+',
            value: 250,
            date: '25.12.19',
          },
          {
            slug: '+',
            value: 45,
            date: '25.12.19',
          },
          {
            slug: '-',
            value: 200,
            date: '25.12.19',
          },
          {
            slug: '+',
            value: 50,
            date: '25.12.19',
          },
          {
            slug: '-',
            value: 250,
            date: '25.12.19',
          },
        ],
        collapse: {
          transactions: false,
        }
      }
    },
    methods: {
      _handleClickPreviousPage () {
        if (window.history.length > 2) {
          this.$router.go(-1)
        } else this.$router.push({ name: 'user.show' })
      },

      _handleClickChooseTariff () {
        this.$modalRouter.push({ name: 'users.tariff.choose.modal' })
      },

       _handleClickReductionTariff () {
        this.$modalRouter.push({ name: 'users.tariff.reduction.modal' })
      },

      _handleClickToggleCollapse (field) {
        if (this.collapse.hasOwnProperty(field)) {
          this.$set(this.collapse, field, !this.collapse[field])
        } else this.$set(this.collapse, field, true)
      },
    },
    mounted () {

    },
  }
</script>

<style scoped></style>



